@mixin onTablet {
  @media (min-width: $tablet-min-width) {
    @content;
  }
};

@mixin onDesktop {
  @media (min-width: $desktop-min-width) {
    @content
  }
};

@mixin paragraphFont {
  font-family: 'Space Mono', monospace;
  font-weight: 400;
}

@mixin contentPaddingInline {
  padding-inline: 20px;

  @include onTablet {
    padding-inline: 72px;
  }

  @include onDesktop {
    max-width: 1020px;
    margin-inline: auto;
    padding-inline: 120px;
  }
};

.container {
  @include contentPaddingInline()
}

@mixin hover($property, $toValue) {
  transition: #{$property}, $effectDuration;
  &:hover {
    #{$property}: $toValue;
  }
};

@mixin pageGrid {
  --columns: 2;
  display: grid;
  column-gap: 20px;
  grid-template-columns: repeat(var(--columns), 1fr);

  @include onTablet {
    --columns: 6;
    column-gap: 24px;
  }

  @include onDesktop {
    --columns: 12;
  }
}

@mixin pageContent {
  display: grid;
  row-gap: 96px;

  @include onTablet {
    row-gap: 120px;
  }
}
